
.carousel-item{
    position: relative;
}
.carousel-title{
    position: absolute;
    height: 60px;
    bottom: 20px;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.4);
    text-align: center;
    line-height: 60px;
    color: #fff;
    font-size: 20px;
}
.carousel-item img{
    height: 360px;
    margin-bottom: 0;
}







